* {
  box-sizing: border-box;
  position: relative;
}

body {
  font-family: 微軟正黑體;
}

ul {
  /* list-style: none; */
}

li {
  padding: 4px;
}

a {
  text-decoration: none;
  color: blue;
}

h4 {
  color: orange;
}

.App {
  /* width: 100%; */
  background-color: #EAEDF2;

}

.App-header {
  width: 100%;
  /* height: 62px; */
  /* background-color: orange; */
}

.App-header-nav {
  height: 62px;
  background-color: #58A9EF;
  box-shadow: 0px 1px 7px rgba(0, 0, 0, 0.4);
}

.nav-center {
  display: flex;
  justify-content: center;
}

.nav-menus, .header-img-box, .article-box, .main {
  width: 1180px;
  height: 100%;
}

.nav-menus {
  display: flex;
  justify-content: space-between;
}

.nav-left, .nav-right {
  display: flex;
}

.nav-logo, .nav-menu {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 70px;
  height: 62px;
  color: white;
  cursor: pointer;
}

.nav-logo {
  font-size: 28px;
}

.nav-menu {
  font-size: 20px;
}

.nav-menu:after {
  content: '';
  position: absolute;
  left: 50%;
  bottom: 0;
  width: 0;
  height: 3px;
  background-color: white;
  transform: translateX(-50%);
  transition: all 0.3s
}

.nav-menu:hover:after {
  width: 100%;
  /* font-size: 30px; */
}

.nav-current:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 3px;
  background-color: white;
  transform: translateX(0);
}

/* .nav-menu:hover > .nav-menu:after {
  width: 100%;
} */


.header-img-box {
  height: 100px;
  margin: 30px auto;
  background-color: #93D1F0;
  height: 502px;
}




.article-box, .main {
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}

.content {
  max-width: 813px;
  width: 100%;
  /* height: 300px; */
}

.tabs {
  max-width: 732px;
  height: 47px;
  display: flex;
  box-shadow: 0px 1px 13px rgba(0, 0, 0, 0.2);
  /* background-color: red; */
  margin-bottom: 22px;
}

.tab {
  width: 50%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  cursor: pointer;
}

.tab-current {
  background-color: #74B9FF;
}

.post-contents {
  width: 783px;
  padding: 22px 0;
  display: flex;
  flex-wrap: wrap;
}

.post-card {
  width: 352px;
  height: 294px;
  background-color: #fff;
  margin-right: 26px;
  margin-bottom: 20px;
  box-shadow: 0px 2px 3px 0 rgba(0, 0, 0, 0.22);
  transition: 0.2s ease-in-out;
  top: 0;
}

.post-card:hover {
  cursor: pointer;
  top: -6px;
  box-shadow: 0px 15px 30px -5px rgba(0, 0, 0, 0.15);
}

.post-card-img {
  width: 100%;
  height: 203px;
  background-color: #86B5FD;
}

.post-card-info {
  margin-top: 13px;
  width: 100%;
  height: 58px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.post-card-date {
  width: 92.6%; 
  font-size: 13px;
  color: #B5B5B5;
}

.post-card-title {
  width: 92.6%; 
  font-size: 18px;
  font-weight: bold;
  
}

.page-numbers {
  width: 100%;
  height: 88px;
  margin-top: 8px;
  margin-bottom: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.page-number {
  width: 46px;
  height: 46px;
  border-radius: 50%;
  background-color: white;
  margin: 0 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  font-weight: bold;
  color: #5BA9F7;
  transition: 0.3s ease-in-out;
  cursor: pointer;
}

.page-number:hover {
  background-color: #c8e4ff;
}

.page-current {
  color: white;
  background-color: #5BA9F7;
}



.notfix {
  width: 364px;
}

.search-form {
  box-shadow: 0px 1px 13px rgba(0, 0, 0, 0.2);
  margin-bottom: 45px;
}

.search-form__input {
  display: block;
  width: 100%;
  padding: 0 48px 0 12px;
  border: 0;
  border-radius: 3px;
  color: #5c6b80;
  font-size: 1em;
  line-height: 45px;  
}

.search-form__submit {
  width: 45px;
  height: 45px;
  background-color: #5ba9f7;
  position: absolute;
  top: 0;
  right: 0;
  padding: 0;
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 0 3px 3px 0;
  color: #fff;
  font-size: 20px;
 
}

.your-profile {
  width: 100%;
  height: 610px;
  background-color: white;
}


.profile-image {
  width: 100%;
  height: 260px;
  background-color: #C8E4FF;
  margin-bottom: 58px;
}

.profile-avatar {
  width: 80px;
  height: 80px;
  background-color: #FFB96C;
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translate(-50%, 50%);
  border: 2px solid white;
  border-radius: 50%;
}

.profile-name {
  margin: 9px 0;
  font-size: 22px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.profile-content {
  /* font-size: 16px; */
  padding: 28px;
  line-height: 30px;
  letter-spacing: 0.5em;
}

/* 按鈕們 */
.option__btn {
  display: flex;
  justify-content: space-between;
}

.back__btn, .edit__btn, .del__btn, .ok__btn, .cancel__btn, .writeComment__submit {
  display: inline-block;
  padding: 10px;
  margin-top: 40px;
  transition: all 0.3s;
  border-radius: 6px;
  cursor: pointer;
}

.back__btn {
  border: 2px solid #afafaf;
}

.edit__btn, .ok__btn, .writeComment__submit {
  margin-right: 16px; 
  font-weight: bold;
  color: #fff;
  border: 2px solid #007BFF;
  background-color: #007BFF;
}

.del__btn {
  font-weight: bold;
  color: #fff;
  border: 2px solid #DC3545;
  background-color: #DC3545;
}

.cancel__btn {
  font-weight: bold;
  color: #fff;
  border: 2px solid #28a745;
  background-color: #28a745;
}

.back__btn:hover {
  background-color: #afafaf;
}

.edit__btn:hover, .ok__btn:hover, .writeComment__submit:hover {
  border: 2px solid #0062cc;
  background-color: #0069d9;
}

.del__btn:hover {
  border: 2px solid #bd2130;
  background-color: #c82333;
}

.cancel__btn:hover {
  border: 2px solid #1e7e34;
  background-color: #218838;
}

/* 單篇文章 */
.single-post-outer {
  /* padding-right: 10px; */
  margin-right: 30px;
}
.single-post {
  padding: 30px;
  background-color: #fff;
  min-height: 700px;
  display: flex;
  flex-direction: column;
  /* border: 1px solid black; */
}

.single-post-title {
  margin: 5px 0;
  padding: 5px 0;
}

table th:first-of-type {
  width: 80px;
  padding: 10px;
}

th:nth-of-type(2) {
  width: 180px;
  padding: 10px;
}

td {
  padding: 10px;
}